<template>
  <div class="pagination">
    <fx-card title="基础用法">
      <fx-pagination :total="30"></fx-pagination>
      <fx-pagination :total="100"></fx-pagination>
      <template #hide>
        <pre v-highlight="base"><code></code></pre>
      </template>
    </fx-card>
    <fx-card title="设置最大页码按钮数">
      <fx-pagination :total="100" :page-count="3"></fx-pagination>
      <fx-pagination :total="100" :page-count="7"></fx-pagination>
      <template #hide>
        <pre v-highlight="pageCount"><code></code></pre>
      </template>
    </fx-card>
    <fx-card title="带边框">
      <fx-pagination :total="100" size="large" border></fx-pagination>
      <fx-pagination :total="100" size="middle" border></fx-pagination>
      <fx-pagination :total="100" size="default" border></fx-pagination>
      <fx-pagination :total="100" size="mini" border></fx-pagination>
      <template #hide>
        <pre v-highlight="border"><code></code></pre>
      </template>
    </fx-card>
    <fx-card title="自定义分页大小">
      <fx-pagination :total="100" :page-size="20"></fx-pagination>
      <fx-pagination :total="100" :page-size="[10,50,100,150]"></fx-pagination>
      <template #hide>
        <pre v-highlight="size"><code></code></pre>
      </template>
    </fx-card>
  </div>
</template>

<script lang="ts">
import { defineComponent } from 'vue'

export default defineComponent({
  name: 'viewPagination',
  setup () {
    const base = `<fx-pagination :total="30"></fx-pagination>
<fx-pagination :total="100"></fx-pagination>`
    const pageCount = `<fx-pagination :total="100" :page-count="3"></fx-pagination>
<fx-pagination :total="100" :page-count="7"></fx-pagination>`
    const border = `<fx-pagination :total="100" size="large" border></fx-pagination>
<fx-pagination :total="100" size="middle" border></fx-pagination>
<fx-pagination :total="100" size="default" border></fx-pagination>
<fx-pagination :total="100" size="mini" border></fx-pagination>`
    const size = `<fx-pagination :total="100" :page-size="20"></fx-pagination>
<fx-pagination :total="100" :page-size="[10,50,100,150]"></fx-pagination>`

    return {
      base,
      pageCount,
      border,
      size
    }
  }
})
</script>

<style scoped lang="scss">
.pagination {
}
</style>
